import React from 'react';
import Comment from './Comment';

import CommentContainer from './CommentsContainer';
let MoneyButton = require('@moneybutton/react-money-button').default

function convertToHex(str) {
  var hex = '';
  for (var i = 0; i < str.length; i++) {
    hex += '' + str.charCodeAt(i).toString(16);
  }
  return hex;
}

function timeDifference(previous) {

  var msPerMinute = 60 * 1000;
  var msPerHour = msPerMinute * 60;
  var msPerDay = msPerHour * 24;
  var msPerMonth = msPerDay * 30;
  var msPerYear = msPerDay * 365;

  var elapsed = Date.now() - previous;

  if (elapsed < msPerMinute) {
       return Math.round(elapsed/1000) + ' seconds ago';   
  }

  else if (elapsed < msPerHour) {
       return Math.round(elapsed/msPerMinute) + ' minutes ago';   
  }

  else if (elapsed < msPerDay ) {
       return Math.round(elapsed/msPerHour ) + ' hours ago';   
  }

  else if (elapsed < msPerMonth) {
      return Math.round(elapsed/msPerDay) + ' days ago';   
  }

  else if (elapsed < msPerYear) {
      return Math.round(elapsed/msPerMonth) + ' months ago';   
  }

  else {
      return Math.round(elapsed/msPerYear ) + ' years ago';   
  }
}

export default class Post extends React.Component {


  constructor(props) {
    super(props);
    this.state = {

      showComments: false,
      // I repurposed tipping functionality to be editing the price of the vote.
      tip: false,
      votePrice:localStorage.getItem('votePrice')
    }
  }





  toggleCommentsHandler = () => {
    this.setState({
      showComments: !this.state.showComments
    })
  }

  toggleTipHandler=()=>{
    if(this.props.loggedIn){
      this.setState({ tip: !this.state.tip })
    } else {
      this.props.replyHandler()//prompts user to Login
    }
  }

  editPriceHandler=()=>{
    console.log()
    this.setState({
      tip:false,
      votePrice:document.getElementsByClassName('price-field')[0].value
    })
  }



  render() {

    var url = decodeURI(this.props.postData.tx.out[0].s5)
    var title = decodeURIComponent(this.props.postData.tx.out[0].s4)
    var username = this.props.postData.tx.out[0].s6
    var showComments = this.props.showCommentsFor == this.props.postData.tx.tx.h
    var selfPost = this.props.postData.tx.out[0].s7 == this.props.userId;
    var previouslyVoted = this.props.postData.voters.includes(this.props.userId);
    var showMoneyButton= !selfPost&& !previouslyVoted 
    var timeago;
    this.props.postData.tx.timeago? timeago=this.props.postData.tx.timeago:timeago =timeDifference(this.props.postData.tx.timestamp);
    var numVotes = this.props.postData.voters.length+1


    var rewardInSatoshis = this.props.postData.reward;
    var rewardInBitcoin = JSON.parse((rewardInSatoshis / 100000000));
    var rewardInDollars = rewardInBitcoin*200;
    var dollarAmountRounded= Math.round(rewardInDollars*100)/100
    var dollarAmountString = dollarAmountRounded.toString();
    var postRightSide = (<div style={{ height: 50 }} />)

    console.log(this.props.defaultVotePrice);

    if (this.props.loggedIn && !this.state.tip) {
      postRightSide = (<div style={{ width:  140 }} ><MoneyButton
        label='vote'

        outputs={[
          {
            amount: 0,
            currency: 'BSV',
            type: 'SCRIPT',
            script: 'OP_RETURN ' + convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('post/vote') + ' ' + convertToHex('vote') + ' ' + convertToHex(this.props.postData.tx.tx.h) + ' ' + convertToHex(this.props.userId)

          }, {
            to: this.props.postData.tx.in[0].e.a,
            amount: this.props.defaultVotePrice,
            currency: 'USD',


          }

        ]}


      /></div>)
    } else if (this.props.loggedIn && this.state.tip) {
      postRightSide = (<div class="price-edit-form-block w-form">
      <form id="email-form-4" name="email-form-4" data-name="Email Form 4" class="form-3">
        <div class="post-price-container"><input type="text" class="text-field price-field w-input"
            maxlength="256" name="name-2" data-name="Name 2" placeholder="Vote Price (USD)" id="name-2" />
          <div class="header-button" onClick={this.editPriceHandler} >
            <div class="header-button-text dropdown-text">set price</div>
          </div>
        </div>
      </form>
      
    </div>)
    }


    if (dollarAmountString.length == 1) {
      dollarAmountString += '.00'
    } 
  


    var replyButton = (
      <div className="post-button" onClick={() => this.props.replyHandler(this.props.postData.tx.tx.h, username, this.props.postData.tx.in[0].e.a)}>
        <div className="metadata post-button-text" >reply</div>
      </div>
    )
    var tipToggle = (
      <div className="post-button"  onClick={this.toggleTipHandler}>
        <div className="metadata post-button-text" onClick={this.toggleTipHandler}>{this.state.tip ? 'cancel' : 'edit price'}</div>

      </div>
    )

    return (

      <div className="post-container"  >
        <div className='post'>
          <div>
            <div className="post-data">
              <div className="reward">${dollarAmountString} <strong></strong></div>
              <div className="post-title" style={{ cursor: 'pointer' }} onClick={() => window.open(url)}>{title}</div>
            </div>
            <div className="post-metadata">
              <div className="metadata"> ({numVotes} vote{numVotes==1?null:'s'})</div>
              <div className="metadata"> {timeago}</div>
              <div className="metadata">{username}</div>

              <div className="metadata" style={{ cursor: 'pointer' }} onClick={() => this.props.toggleComments(this.props.postData.tx)} > {showComments ? 'hide comments ▼' : 'show comments ▶'}</div>
              
              {selfPost ? null : [replyButton, tipToggle]}
            </div>
          </div>
          {!showMoneyButton && !this.state.tip? <div style={{ height: 50, width:120, display:'flex',alignItems:'center', justifyContent:'center' }}>{previouslyVoted?"Voted! 🗳️":null}</div> : postRightSide}

        </div>

        {showComments ? <CommentContainer replyHandler={this.props.replyHandler} userId={this.props.userId} comments={this.props.comments} /> : null}
      </div>



    );
  }
}

